ody {\n  position: relative;\n  float: left;\n  width: 100%;\n  margin: 0;\n  font-family: 'Work Sans', sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  padding: 0;\n  background: #fff;\n  color: #9ca3af;\n  font-size: 16px;\n  overflow-x: hidden;\n\n}\n\nh1,h2,h3 {\n  font-family: \"Londrina\";\n  font-weight:bold;\n  color: #333;\n}\n\nh1 {\n  font-size: $xl;\n  margin: 0.83em 0;\n}\n\nh2 {\n  font-size: $larger;\n  margin: 0.83em 0;\n}\n\nh3 {\n  font-size: $large;\n  margin: 0.83em 0;\n}\n\n.u {\n  color: #8ec1de;\n  text-decoration: underline;\n  text-transform: uppercase;\n}\n\n.l {\n  text-transform: lowercase;\n}\n\n\na {\n  text-decoration: none;\n  color: #1a1b1f;\n  @include transition(all);\n  &:hover {\n    color: #6DD0F7;\n  }\n}\n\np {\n  line-height: calc(16px * 1.5);\n  margin: 0 0 0.5rem;\n  &:last-child {\n    margin-bottom: 0;\n  }\n}\n\nb {\n  color: #333;\n}\n\nheader {\n  position: relative;\n  display: block;\n  margin: 0 auto;\n  width: 100%;\n  max-width: 1280px;\n  height: 4.75rem;\n  line-height: 4.75rem;\n  padding: 0 40px;\n  display: grid;\n  grid-template-columns: 1fr 1fr 1fr;\n  .logo{\n    img {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      @include transform(translate(-50%,-50%));\n      height: 3rem;\n    }\n  }\n  .social {\n    position: absolute;\n    right: 40px;\n    height: 4.75rem;\n    a {\n      display: inline-block;\n      margin-left: 2rem;\n      width: 30px;\n      &:first-child {\n        margin-left: 0;\n      }\n      svg {\n        height: 30px;\n        width: 30px;\n        margin: calc((4.75rem - 30px) / 2 ) 0;\n        @include transition(all);\n        .fill {\n          fill:#333;\n        }\n      }\n      &:hover {\n        svg {\n          @include transform(scale(1.2,1.2));\n          .fill {\n            fill:#6DD0F7;\n          }\n        }\n      }\n    }\n  }\n}\n\n.treasury {\n  left: 40px;\n  position: absolute;\n  top: 50%;\n  @include transform(translateY(-50%));\n  line-height: 20px;\n  font-weight: 500;\n  a {\n    text-decoration: none;\n    color: inherit;\n  }\n  span {\n    font-size: $small;\n    text-transform: uppercase;\n  }\n  #balance {\n    display: block;\n    color: #1a1b1f;\n    font-weight: bold;\n    img {\n      height: 20px;\n      position: relative;\n      float: left;\n      margin-right: .5rem;\n    }\n  }\n}\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n#mint {\n  @include aspect-ratio(15,7);\n  background-color: #7bcce8;\n  margin-bottom: 6.25rem;\n  width: calc(100% - 80px);\n  border-radius: 24px;\n  max-width: 1200px;\n  background-image: url('https://uma.sebastien.wtf/img/bg.jpg');\n  background-size: cover;\n  background-position: bottom;\n  a {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    @include transform(translate(-50%,-50%));\n    height:100%;\n    line-height: 3.75rem;\n    padding: 0 1.5625rem;\n    text-transform: uppercase;\n    text-decoration: none;\n    @include transition(all);\n\n    font-family: \"Londrina\";\n    font-weight:bold;\n    color: #000;\n    z-index: 1;\n    font-size: $xl;\n    span {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      @include transform(translate(-50%,-50%));\n      @include transition(all);\n    }\n    img {\n      position: absolute;\n      height: 100%;\n      width: auto;\n      top: 0;\n      left: 50%;\n      transform: translate(-50%, 0);\n      transform-origin: center center;\n      @include transition(all);\n    }\n\n    &:hover {\n      span {\n        @include transform(translate(-50%,-50%) scale(1.05, 1.05));\n      }\n      img {\n        @include transform(translate(-50%,0) scale(1.1, 1.1));\n      }\n      // color: #fff;\n      // background: #1a1b1f;\n    }\n  }\n}\n\n.aspiring {\n  font-size: $large;\n  text-align: center;\n  font-family: \"Londrina\";\n  font-weight:bold;\n  color: #333;\n  h3 {\n    display: block;\n    width: 100%;\n